@import "./variables";
@import "./animation";

.quotes{
  height: 100%;
  width: 100%;
  padding: 6rem 8rem 4rem 8rem;

  display: flex;
  flex-direction: column;
  gap: 3rem;

  &__item{
    position: relative;
    width: auto;
    height: auto;
    padding: 2rem 3rem 6rem 3rem;

    background-color:rgba($color-white, .65);
    box-shadow: 0 0rem 2rem 1rem rgba($color-primary-dark, 0.1);
  }

  &__text{
    font-size: 2rem;
    font-family: 'Dancing Script', cursive;
    text-align: center;

    &::after{
      content: ".\00a0\"";
    }

    &::before{
      content: "\"\00a0";
    }
  }

  &__note{
    font-style: italic;
    position: absolute;
    bottom: 2rem;
    right: 3rem;
  }
}

.blogs {
  height: 100%;
  padding: 4rem 5rem;

  &__item {
    position: relative;
    background-color: rgba($color-white, 0.9);
    box-shadow: 0 0rem 2rem 1rem rgba($color-primary-dark, 0.1);
    padding: 2.5rem 2.5rem;

    cursor: pointer;
    text-transform: capitalize;

    height: 30rem;
    padding-top: 2rem;
    animation: scale-out-1 500ms ease-in-out forwards;

    display: block;

    &:hover {
      animation: scale-in-1 500ms ease-in-out forwards;
    }
  }

  &__link{
    text-decoration: none;
  }

  &__title {
    width: 100%;
    height: 30%;
    padding-top: 1rem;
    
    font-size: 1.8rem;
    font-family: 'Bad Script', cursive;

    white-space: pre-wrap;
    overflow: hidden; // ẩn các nội dung khi kích thước lớn hơn chiều rộng khối bên ngoài
    text-overflow: ellipsis; //thêm 3 dấu chấm ở cuối
    -webkit-line-clamp: 2; // số dòng muốn hiển thị
    -webkit-box-orient: vertical;
    display: -webkit-box;

    color: rgba($color-secondary-dark-1, 1);
  }

  &__image {
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    height: 70%;
    width: 100%;
  }
}

.home {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  width: 100vw;

  &__wrapper {
    width: auto;
    height: auto;
  }

  &__avatar {
    display: flex;
    justify-content: center;
    align-items: center;
    animation: move-down 1s ease-in-out forwards;
  }
}

.about {
  width: 100%;
  height: 100%;

  &__wrapper {
    background-color: rgba($color-white, 0.65);
    color:rgba($color-waves-dark,1);

    margin: 2rem 3rem 2rem 3rem;
    padding: 10rem 6rem;
    border-radius: 1rem;
    width: auto;
    min-height: calc(100vh - 4rem);
  }

  &__content {
    padding-top: 2rem;
    font-size: 2.6rem;
  }
}

.resume {
  position: relative;

  margin: 2rem 3rem;
  font-size: 2rem;
  padding: 10rem 3rem;
  border-radius: 1rem;
  font-family: 'EB Garamond', serif;

  color: rgba($color-waves-dark, 1);
  background-color: rgba($color-white, .5 );
  
  height: 100%;

  &__middle-line {
    position: absolute;
    top: 10rem;
    left: 49.75%;
    height: 80%;
    width: 0.5rem;
    z-index: 0;
    background-color: rgba($color-primary-dark, 1);
  }

  &__info {
  }

  &__name {
    text-transform: uppercase;
    font-size: 3rem;
    font-weight: bold;
  }

  &__pos {
    font-size: 1.8rem;
    font-style: italic;
  }

  &__inline {
    display: flex;
    justify-content: space-around;
    align-items: center;

    &--start {
      justify-content: flex-start !important;
      gap: 1.5rem;
    }
  }

  &__contact {
    margin-top: 1rem;
  }

  &__date {
    text-align: right;
    font-style: italic;
    font-size: 1.2rem;
    margin-bottom: 1rem;
  }

  &__intro {
    position: relative;
    height: 100%;
    width: 100%;
    text-align: left;
    display: flex;
  }

  &__title--about {
    position: absolute;
    top: 0;
    left: 0;
    font-size: 1.4rem;
    padding: 0.1rem 1rem;
    background-color: rgba($color-primary-dark, 0.5);
  }

  &__prolang {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }

  &__skills-list {
    text-transform: capitalize;
  }

  &__skill-item {
    width: 100%;
    height: auto;
    padding: 1rem;
    background-color: rgba($color-white, 0.4);
    margin-top: 1rem;
    display: flex;
    align-items: center;
    border-left: 0.4rem solid rgba($color-primary-dark, 0.8);
  }

  &__detail {
    text-align: left !important;
    padding-left: 2rem;
    background-color: rgba($color-primary-light, 0.9);
    padding: 1.5rem 2.5rem;

    &--education {
      background-color: rgba($color-white, 0.3);
      padding: 1rem 2rem;
    }

    &--about {
      position: absolute;
      top: 3rem;
      left: 1rem;
    }
  }

  &__lang {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    align-items: center;
    gap: 2rem;
  }

  &__hobby {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 3rem;
    background-color: rgba($color-white, 0.1);
    margin: 0.5rem 0;
    padding: 0.5rem 1.5rem;
  }

  &__tag {
    width: auto;
    height: auto;
    padding: 0.5rem 1.5rem;
    font-weight: bold;
    font-size: 2rem;
    text-transform: capitalize;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: aliceblue;
    border: solid 0.5rem rgba($color-primary-dark, 1);
    position: absolute;
    top: 50%;

    &--education {
      right: 0%;
      transform: translate(0, -50%);
    }

    &--skills {
      left: 8%;
      transform: translate(-8%, -50%);
    }

    &--project {
      right: 11%;
      transform: translate(-11%, -50%);
    }
    &--languages {
      right: 10%;
      transform: translate(-10%, -50%);
    }

    &--hobbies {
      left: 0%;
      transform: translate(0, -50%);
    }
  }

  &__icon {
    text-align: center;
    height: 8rem;
    width: 8rem;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    background-color: aliceblue;
    color: rgba($color-secondary-dark, 1);
    box-shadow: 0 0.5rem 2rem 1rem rgba($color-primary-light, 0.05);
    z-index: 1;
  }
}
